  right-text="历史消息"
  left-arrow
  @click-left="onClickBack"
  @click-right="enterHistory"
/>
  <div class="g-inherit m-article">
    <!-- <Row class="b-b-1">
      <Col span="24">
      <div class="w-100 " style="height: 55px">
        <span class="fl m-l-10 h60" @click="enterNameCard">{{sessionName}}</span>
        <span class="fr m-r-10 h60" @click="enterHistory">历史消息</span>
        <span class="fr m-r-10 h60" @click="enterTeamInfo" v-show="scene==='team'">资料</span>
      </div>
      </Col>
    </Row> -->
    <div class="m-chat-main" id="m-chat-main">
      <chat-list
        type="session"
        :msglist="msglist"
        :userInfos="userInfos"
        :myInfo="myInfo"
        @msgs-loaded="msgsLoaded"
      ></chat-list>
    </div>
    <chat-editor
      style="position: absolute;bottom: 0px"
      type="session"
      :scene="scene"
      :to="to"
    ></chat-editor>
  </div>
  </div>
