<script>
  import ChatEditor from './components/ChatEditor'
  import ChatList from './components/ChatList'
  import util from './utils'
  import pageUtil from './utils/page'
  import { Toast } from "vant";
  import cookie from '../../components/im/utils/cookie'
  export default {
    /*eslint-disable*/
    components: {
      ChatEditor,
      ChatList
    },
    data () {
      return {
        /* 监听页面的高度*/
        /*
         screenHeight: document.body.clientHeight,
         */
        leftBtnOptions: {
          backText: ' ',
          preventGoBack: true,
        },
      }
    },
    watch: {
      $route(to, from) {
        // console.log('watch', to, from)
      },
      sessionId(val) {
        // console.log('sessionId:', val)
        
        this.$store.dispatch('setCurrSession', val)
      },
    },
    // 进入该页面，文档被挂载
    mounted () {
    //   this.$store.dispatch('connect')
      this.$store.dispatch('showLoading')
      // 此时设置当前会话
      if (cookie.readCookie('uid')==null) {
         Toast("token错误~，请重新登录··")
         setTimeout(() => {
        this.$router.push("/login");
         },300)
         
      }
      this.$store.dispatch('setCurrSession', this.sessionId)
      pageUtil.scrollChatListDown()
      setTimeout(() => {
        this.$store.dispatch('hideLoading')
      }, 1000)
    },
    updated () {
      pageUtil.scrollChatListDown()
    },
    // 离开该页面，此时重置当前会话
    destroyed () {
      this.$store.dispatch('resetCurrSession')
    },
    computed: {
      sessionId () {
        let sessionId = this.$route.params.sessionId
        return sessionId
      },
      sessionName () {
        let sessionId = this.sessionId
        let user = null
        if (/^p2p-/.test(sessionId)) {
          user = sessionId.replace(/^p2p-/, '')
          if (user === this.$store.state.userUID) {
            return '我的手机'
          }
          let userInfo = this.userInfos[user] || {}
          return util.getFriendAlias(userInfo)
        } else if (/^team-/.test(sessionId)) {
          return '群'
        }
      },
      scene () {
        // console.log('scene', util.parseSession(this.sessionId).scene)
        return util.parseSession(this.sessionId).scene
      },
      to () {
        return util.parseSession(this.sessionId).to
      },
      myInfo () {
        return this.$store.state.myInfo
      },
      userInfos () {
          
        //  console.log(this.$store.state.userInfos)
        return this.$store.state.userInfos
      },
      msglist () {
        let msgs = this.$store.state.currSessionMsgs
        return msgs
      }
    },
    methods: {
      enterHistory () {
        this.$router.push({
          path: `/chatHistory/${this.sessionId}`
        })
      },
      onClickBack () {
        // location.href = '#/contacts'
        this.$store.state.sessionlist = [];
       this.$router.back(-1);
      },
      msgsLoaded () {
        pageUtil.scrollChatListDown()
      },
      enterNameCard () {
        if (/^p2p-/.test(this.sessionId)) {
          let account = this.sessionId.replace(/^p2p-/, '')
          if (account === this.$store.state.userUID) {
            this.$router.push({
              path: `/im_web/general`
            })
            return
          }
          this.$router.push({path: `/im_web/namecard/${account}`})
        }
      },
      enterTeamInfo(){
        let account = this.sessionId.replace(/^team-/, '')
        // console.log(account)
        this.$router.push({path: `/im_web/teamInfo/${account}`})
      }
    },
    //  created() {
    //   const searchText = 'admin'
    //   this.searchText = searchText
    //   console.log('searchText', searchText)
    //   console.log(this.$store.dispatch('searchUsers', {
    //     accounts: [this.searchText]
    //   }))
    // }
  }
</script>

