    display: block;

    margin: 0 auto;

    width: 90%;

    -moz-box-sizing: border-box;

         box-sizing: border-box;

    line-height: 1.6;

    border: 1px solid #ccc;
}
.g-window .m-room-entry .u-box .album {

    position: relative;

    height: 6.4rem;

    font-size: 0.9rem;

    overflow: hidden;

    background-color: #000;
}
.g-window .m-room-entry .u-box .album .pic {

    position: absolute;

    width: auto;

    height: inherit;

    left: 50%;

    top: 50%;

    -webkit-transform: translate(-50%, -50%);

       -moz-transform: translate(-50%, -50%);

            transform: translate(-50%, -50%);
}
.g-window .m-room-entry .u-box .album .status {

    position: absolute;

    bottom: 0;

    left: 0;

    width: 100%;

    height: 1.6rem;

    line-height: 1.6rem;

    text-align: left;

    color: #333;

    background-color: #fff;

    opacity: 0.8;
}
.g-window .m-room-entry .u-box .album .status strong {

    margin-left: 0.4rem;
}
.g-window .m-room-entry .u-box .album .status em {

    margin-right: 0.4rem;

    float: right;

    color: inherit;
}
.g-window .m-room-entry .u-box .desc {

    margin-top: -1px;

    text-align: left;

    text-indent: 0.5rem;

    line-height: 1.6rem;

    min-height: 1.6rem;

    color: #999;

    background-color: #fff;
}
/* 聊天室内页 */
.g-window .m-room-album {

    position: absolute;

    display: block;

    top: 0;

    left: 0;

    width: 100%;

    height: 12rem;

    background-color: #000;

    z-index: 2;
}
.g-window .m-room-album .img-wraper {

    width: 50%;

    margin-left: 50%;

    height: inherit;
}
.g-window .m-room-album .room-img {

    position: relative;

    display: block;

    height: inherit;

    width: auto;

    -webkit-transform: translate(-50%, 0);

       -moz-transform: translate(-50%, 0);

            transform: translate(-50%, 0);
}
/* 聊天室相关页面 */
.g-window .m-room-tabs {

    position: absolute;

    top: 12rem;

    left: 0;

    height: 3rem;

    width: 100%;

    z-index: 3;
}
.g-window .m-room-tabs .u-tab {

    position: relative;

    display: inline-block;

    -moz-box-sizing: border-box;

         box-sizing: border-box;

    width: 33.333%;

    height: inherit;

    line-height: 3rem;

    text-align: center;

    cursor: pointer;
}
.g-window .m-room-tabs .u-tab.active {

    color: #0091e4;

    border-bottom: #0091e4 0.4rem solid;
}
.g-window .m-room-container {

    position: absolute;

    display: block;

    -moz-box-sizing: border-box;

         box-sizing: border-box;

    top: 0;

    left: 0;

    padding-top: 15rem;

    width: 100%;

    height: 100%;
}
.g-window .room-cnt {

    position: relative;

    display: block;

    height: 100%;

    width: 100%;
}
/*
* 元件
*/
/* 外面嵌套g-window 为了覆盖weui默认样式 */
/* 白底输入框 */
.g-window .u-ipt-default {

    color: #999;
}
.g-window .u-ipt-default input::-webkit-input-placeholder {

    color: #e0e0e0;
}
.g-window .u-ipt-default input::-moz-placeholder {

    color: #e0e0e0;
}
.g-window .u-ipt-default input::placeholder {

    color: #e0e0e0;
}
/* 透明底输入框 */
.g-window .u-ipt-transparent {

    color: #fff;

    background-color: transparent;
}
.g-window .u-ipt-transparent input::-webkit-input-placeholder {

    color: #fff;

    opacity: 0.5;
}
.g-window .u-ipt-transparent input::-moz-placeholder {

    color: #fff;

    opacity: 0.5;
}
.g-window .u-ipt-transparent input::placeholder {

    color: #fff;

    opacity: 0.5;
}
/* 双选切换控件 */
.g-window .u-switch .weui-switch:checked, .g-window .u-switch .weui-switch-cp__input:checked ~ .weui-switch-cp__box {

    background-color: #0091e4;

    border-color: #0091e4;
}
.g-window .u-card {

    margin: 1rem 0;
}
.g-window .u-card .weui-cells {

    background-color: #fff;
}
.g-window .u-card .icon {

    display: inline-block;

    margin-right: 0.8rem;

    width: 2rem;

    height: 2rem;
}
.g-window .u-search {

    margin: 0;

    margin-bottom: 2rem;

    height: 2rem;
}
.g-window .u-search .weui-cells:after, .g-window .u-search .weui-cells:before {

    border-color: #f0f0f0;
}
/* 列表 */
.g-window .u-list {

    width: 100%;
}
.g-window .u-list-item {

    min-height: 2rem;

    vertical-align: middle;
}
.g-window .u-list-item .icon {

    display: inline-block;

    margin-right: 0.6rem;

    width: 2rem;

    height: 2rem;
}
/* 会话删除tag */
.g-window .u-tag-del {

    position: absolute;

    right: 0;

    top: 0;

    width: 24%;

    height: 100%;

    text-align: center;

    color: #fff;

    background-color: #ed0e0e;

    -webkit-transition: all 0.5s;

    -moz-transition: all 0.5s;

    transition: all 0.5s;

    -webkit-transform: translate3d(100%, 0, 0);

       -moz-transform: translate3d(100%, 0, 0);

            transform: translate3d(100%, 0, 0);
}
.g-window .u-tag-del::after {

    position: relative;

    content: "删除";

    font-size: 16px;

    font-size: 1rem;

    top: 32%;

    margin-top: -8px;

    margin-top: -0.5rem;
}
.g-window .u-tag-del.active {

    -webkit-transform: translate3d(0, 0, 0);

       -moz-transform: translate3d(0, 0, 0);

            transform: translate3d(0, 0, 0);
}
/*时间提示*/
.g-window .u-session-time {

    display: inline-block;

    position: absolute;

    right: 1.8rem;

    top: 1.1rem;
}
/* 未读数标签 */
.g-window .u-unread {

    display: inline-block;

    position: absolute;

    right: 0.4rem;

    top: 1.1rem;

    padding: 2px;

    min-width: 1rem;

    min-height: 1rem;

    line-height: 1rem;

    font-size: 12px;

    background-color: #f00;

    color: #fff;

    text-align: center;

    border-radius: 0.8rem;
}
/* 消息记录 */
.g-window .u-msg {

    position: relative;

    padding: 0.2rem 0;
}
/* 用户头像 */
.g-window .u-msg .msg-head {

    position: relative;

    display: inline-block;

    top: 0.1rem;

    margin: 0;

    margin-right: 0.6rem;

    margin-left: 0.6rem;

    padding: 0;

    width: 2rem;

    height: 2rem;

    vertical-align: top;
}
.g-window .u-msg .msg-head img {

    position: relative;

    display: inline-block;

    margin: 0;

    width: inherit;

    height: inherit;
}
.g-window .u-msg .msg-user {

    font-style: italic;

    color: #999;
}
.g-window .u-msg .msg-user em {

    font-weight: normal;

    margin-right: 1rem;
}
.g-window .u-msg .msg-text {

    position: relative;

    display: inline-block;

    max-width: 54%;

    min-height: 1.2rem;

    word-break: break-word;

    height: auto;

    line-height: 1.2rem;

    font-size: 0.9rem;

    border: none;
}
.g-window .u-msg .msg-text img {

    position: relative;

    display: inline-block;

    margin: 0;

    padding: 0;

    max-width: 100%;

    vertical-align: bottom;
}
.g-window .u-msg .msg-text img.emoji-small {

    width: 1.4rem;

    height: 1.4rem;

    vertical-align: bottom;
}
.g-window .u-msg .msg-text img.emoji-big {

    width: 3rem;

    height: 3rem;

    vertical-align: middle;
}
.g-window .u-msg .msg-text img.emoji-big {

    width: 4.8rem;

    height: 4.8rem;

    vertical-align: middle;
}
.g-window .u-msg .msg-text embed, .g-window .u-msg .msg-text video {

    position: relative;

    display: inline-block;

    max-width: 100%;

    max-height: 8rem;

    vertical-align: bottom;

    background-color: #000;

    text-align: center;

    color: #fff;
}
.g-window .u-msg .msg-text .button {

    margin: 0.1rem 0;

    padding: 0.1rem 0.2rem;

    border: 1px solid #fff;

    border-radius: 0.2rem;

    background-color: #e5f4ff;

    color: #666;
}
.g-window .u-msg .msg-text::before, .g-window .u-msg .msg-text::after {

    content: ' ';

    position: absolute;

    top: 12.8px;

    top: 0.8rem;

    border-top: 6.4px solid transparent;

    border-top: 0.4rem solid transparent;

    border-bottom: 6.4px solid transparent;

    border-bottom: 0.4rem solid transparent;

    width: 0;

    height: 0;
}
.g-window .u-msg .msg-text.notify {

    max-width: 100%;
}
.g-window .u-msg .msg-failed {

    position: relative;

    float: right;

    margin-right: 0.2rem;

    vertical-align: top;

    font-size: 0.92rem;

    line-height: 2.0rem;
}
.g-window .u-msg .msg-link {

    display: block;

    position: absolute;

    bottom: -2rem;

    right: 0;

    min-width: 4rem;

    min-height: 1.2rem;

    word-break: normal;

    height: auto;

    line-height: 1.2rem;

    font-size: 1.0rem;

    border: none;
}
.g-window .u-msg .msg-link a {

    color: #0091e4;

    text-decoration: underline;

    float: right;
}
.g-window .u-msg .notification {

    font-size: 0.8rem;

    text-align: center;

    color: #ccc;
}
.g-window .u-msg .notify {

    color: #f50;

    font-size: 0.8rem;
}
.g-window .u-msg.session-chat {

    padding: 0.6rem 0;

    clear: both;
}
.g-window .u-msg.session-chat .msg-text {

    padding: 0.4rem;

    margin-bottom: 0.8rem;

    border-radius: 0.4rem;
}
.g-window .u-msg.session-chat .msg-robot {

    min-width: 3rem;

    margin-bottom: 3rem;
}
.g-window .u-msg.session-chat.item-you .msg-head, .g-window .u-msg.session-chat.item-you .msg-text {

    float: left;
}
.g-window .u-msg.session-chat.item-you .msg-text {

    background-color: #5cacde;

    color: #fff;
}
.g-window .u-msg.session-chat.item-you .msg-text::before {

    right: 99%;

    border-right: 6.4px solid #5cacde;

    border-right: 0.4rem solid #5cacde;
}
.g-window .u-msg.session-chat.item-you .msg-text::after {

    background: none;
}
.g-window .u-msg.session-chat.item-me .msg-head, .g-window .u-msg.session-chat.item-me .msg-text {

    float: right;
}
.g-window .u-msg.session-chat.item-me .msg-text {

    color: #666;

    background-color: #e5f4ff;
}
.g-window .u-msg.session-chat.item-me .msg-text::before {

    background: none;
}
.g-window .u-msg.session-chat.item-me .msg-text::after {

    left: 99%;

    border-left: 6.4px solid #e5f4ff;

    border-left: 0.4rem solid #e5f4ff;
}
.g-window .u-msg.item-time {

    font-size: 0.8rem;

    text-align: center;

    color: #ccc;
}
.g-window .u-msg.item-tip {

    font-size: 0.8rem;

    text-align: center;
}
.g-window .u-msg.item-tip .tip {

    margin: 0 auto;

    width: 10rem;

    padding: 0.1rem;

    background-color: #e0e0e0;

    border-radius: 0.4rem;
}
.g-window .u-error {

    color: #ed0e0e;
}
.g-window .u-icon {

    display: inline-block;

    width: inherit;

    height: inherit;
}
.g-window .u-icon.icon-file {

    background-position: -7.6rem -7rem;
}
.g-window .u-icon {

    background-size: 20rem;

    background-image: url(http://yx-web.nos.netease.com/webdoc/h5/im/icons.png);
}
.g-window .u-icon-img {

    position: relative;

    width: inherit;

    height: inherit;
}
.g-window .u-icon-img img {

    position: relative;

    margin: 0;

    padding: 0;

    width: 100%;

    height: 100%;
}
.g-window .u-circle {

    border-radius: 50%;
}
/* 进入切页动画，可根据开发这需要自由开发 */
.forward-enter-active, .forward-leave-active {

    position: absolute;

    left: 0;

    top: 0;

    -webkit-transition: all 0.5s;

    -moz-transition: all 0.5s;

    transition: all 0.5s;

    z-index: 0;
}
.forward-leave {

    -webkit-transform: translate3d(0, 0, 0);

       -moz-transform: translate3d(0, 0, 0);

            transform: translate3d(0, 0, 0);
}
.forward-leave-to {

    -webkit-transform: translate3d(-100%, 0, 0);

       -moz-transform: translate3d(-100%, 0, 0);

            transform: translate3d(-100%, 0, 0);
}
.forward-enter {

    -webkit-transform: translate3d(100%, 0, 0);

       -moz-transform: translate3d(100%, 0, 0);

            transform: translate3d(100%, 0, 0);
}
.forward-enter-to {

    -webkit-transform: translate3d(0, 0, 0);

       -moz-transform: translate3d(0, 0, 0);

            transform: translate3d(0, 0, 0);
}
/* 离开切页动画，可根据开发这需要自由开发 */
.backward-enter-active, .backward-leave-active {

    position: absolute;

    left: 0;

    top: 0;

    -webkit-transition: all 0.5s;

    -moz-transition: all 0.5s;

    transition: all 0.5s;

    z-index: 0;
}
.backward-leave {

    -webkit-transform: translate3d(0, 0, 0);

       -moz-transform: translate3d(0, 0, 0);

            transform: translate3d(0, 0, 0);
}
.backward-leave-to {

    -webkit-transform: translate3d(100%, 0, 0);

       -moz-transform: translate3d(100%, 0, 0);

            transform: translate3d(100%, 0, 0);
}
.backward-enter {

    -webkit-transform: translate3d(-100%, 0, 0);

       -moz-transform: translate3d(-100%, 0, 0);

            transform: translate3d(-100%, 0, 0);
}
.backward-enter-to {

    -webkit-transform: translate3d(0, 0, 0);

       -moz-transform: translate3d(0, 0, 0);

            transform: translate3d(0, 0, 0);
}

/*# sourceMappingURL=data:application/json;base64, */
</style>


<style type="scss">
  .b-b-1 {
    border-bottom: 1px solid #F0F0F0;
  }

  a {
    color: black;
    text-decoration: none;
  }

  .tl {
    text-align: left;
  }

  .tc {
    text-align: center;
  }

  .tr {
    text-align: right;
  }

  .m-chat-main {
    /* 聊天页面有输入框 */
    padding: 0 0 $height_editor 0;
    /* 聊天历史记录没有输入框 */
    &.m-chat-history {
      padding-bottom: 0;
    }
    .m-chat-list {
      position: relative;
      display: block;
      box-sizing: border-box;
      padding: 1rem 2%;
      width: 100%;
      height: 100%;
      overflow-y: scroll;
      overflow-x: hidden;
    }
  }

  .m-chat-main {
    overflow: hidden;
  }
  .cnt{
      height: 273px;
    position: absolute;
    overflow-y: scroll;
    overflow-x: scroll;
  }
</style>



